/* ============================================= */
/*              Typography                       */
/* ============================================= */

* {
	font-family: 'Lato', sans-serif;
	color: #777;
	box-sizing: border-box;
}

.headline-primary {
	color: #58e1c1;
	margin-left: 10px;
}

.headline-secondary {
	font-weight: 300;
	color: #777;
}

.link {
	color: #51ddfc;
	font-weight: 700;
}

.blockquote {
	font-size: 1.5em;
	font-weight: 300;
	font-style: italic;
	margin-left: 20px;
	padding-left: 20px;
	border-left: 8px solid #58e1c1;
	border-radius: 5px;
}
